ul {
    background-color: #FBAF37;
    font-size: 16px;
    height: 60px;
    line-height: 2.5
}

a {
    color: #F7f7f7;
    height: 100%;
    padding-top: 15px;
}

a:hover {
    color: #DC851F;
}

.nav-link:hover {
    border: 0px;
}
.home-div {
    margin-top: 30px;
}

.home-buttons {
    width: 85%;
    margin: auto;
    margin-top: 30px;
}

.home-button {
    height: 225px;
    width: 45%;
    margin: 20px;
    border-radius: 125px;
    box-shadow:0 0 8px #605856;
    font-family: 'Abril Fatface', cursive;
    font-size: 40px;
    color: white;
}

.home-button:hover {
    cursor: pointer;
    -webkit-transform: scale(1.05);
            transform: scale(1.05)
}

#button1 {
    border-color: #FBAF37;
    background-color: #FBAF37;
}

#button2 {
    border-color: #F75133;
    background-color: #F75133;
}

#button3 {
    border-color: #66483F;
    background-color: #66483F;
}

#button4 {
    border-color: #50A3A4;
    background-color: #50A3A4;
}
.collection-div {
    margin-top: 30px;
}

.collection-items-div {
    margin: 30px auto 30px auto;
    width: 90%;
    box-shadow:0 0 8px #605856;
    font-family: 'Abril Fatface', cursive;
    padding-bottom: 10px;
}

.categories {
    list-style-type: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin: 0;
    padding: 0;
    line-height: 3.0;
    color: #66483F;
    background-color: white;
    font-family: 'Questrial', sans-serif;
}

.item-card-div {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    padding: 0 5% 0 5%;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
}

.img-container > img {
    margin-top: 5%;
    width: 100%
  }

.card {
    border-radius: 2px;
    height: 470px;
    margin: 5px;
    margin-bottom: 2%;
    position: relative;
    width: 300px;
    text-align: left;
    font-family: 'Questrial', sans-serif;
}

.card:hover {
    border-color: #FBAF37;
}

.card-title {
    color: #F75133;
}

.card-text {
    color: #66483F;
}

.btn {
    background-color: #50A3A4;
    border-color: #50A3A4;
    height: 40px;
    padding-top: 8px;
}
.post-item-div {
    margin-top: 30px;
}

.form-div {
    margin: 35px auto 30px auto;
    width: 60%;
    box-shadow:0 0 8px #605856;
    padding-bottom: 1px;
}

form {
    margin: 5% 0 5% 6%;
    color: #F75133;
    font-family: 'Questrial', sans-serif;
}

#post-submit-btn {
    margin-top: 2%;
    padding-top: 10px;
    height: 45px;
    width: 100%;
    border-radius: 125px;
}

#map {
    height: 100%;
    width: 100%;
}

.form-head {
    height: 100%;
    padding: 10px 0 2px 15px;
}

.form-control {
    border: solid 1px #E7E6F7;
}
.delete-btn {
  float: right;
  color: #e74944;
}

.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

